.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.darkTheme .ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  background-color: rgba(255, 255, 255, 0.04);
}
.darkTheme .ant-card-hoverable:hover {
  box-shadow: 0 1px 2px -2px #000000, 0 3px 6px 0 #000000, 0 5px 12px 4px #000000 !important;
}
.darkTheme .ant-table-tbody > tr.ant-table-row:hover > td,
.darkTheme .ant-table-thead th.ant-table-column-has-sorters:hover,
.darkTheme .ant-table-filter-trigger-container-open,
.darkTheme .ant-table-filter-trigger-container:hover,
.darkTheme .ant-table-thead th.ant-table-column-has-sorters:hover .ant-table-filter-trigger-container:hover {
  background: #262626;
}
.darkTheme .ant-menu-item-selected {
  background-color: var(--primary) !important;
}
.darkTheme .ant-menu-item-selected > a {
  color: #ffffff !important;
}
.darkTheme .ant-table-filter-dropdown,
.darkTheme .ant-dropdown-menu,
.darkTheme .ant-picker-panel-container,
.darkTheme .ant-cascader-menus,
.darkTheme .ant-select-dropdown {
  background: #141414;
}
.darkTheme .weather-body {
  background-color: rgba(0, 0, 0, 0.7) !important;
}
.darkTheme .ant-tag-processing {
  background: #308ee0;
  color: var(--heading-color);
  border-color: rgba(48, 142, 224, 0.006);
}
.darkTheme .ant-tag-success {
  background: #00ce68;
  color: var(--heading-color);
  border-color: rgba(0, 206, 104, 0.006);
}
.darkTheme .ant-tag-error {
  background: #e65251;
  color: var(--heading-color);
  border-color: rgba(230, 82, 81, 0.006);
}
.darkTheme .ant-tag-warning {
  background: #ffaf00;
  color: var(--heading-color);
  border-color: rgba(255, 175, 0, 0.006);
}
.darkTheme .ant-tag-default {
  color: var(--heading-color);
}
.darkTheme .ant-menu-submenu-vertical > .ant-menu-submenu-title .ant-menu-submenu-arrow::before,
.darkTheme .ant-menu-submenu-vertical-left > .ant-menu-submenu-title .ant-menu-submenu-arrow::before,
.darkTheme .ant-menu-submenu-vertical-right > .ant-menu-submenu-title .ant-menu-submenu-arrow::before,
.darkTheme .ant-menu-submenu-inline > .ant-menu-submenu-title .ant-menu-submenu-arrow::before,
.darkTheme .ant-menu-submenu-vertical > .ant-menu-submenu-title .ant-menu-submenu-arrow::after,
.darkTheme .ant-menu-submenu-vertical-left > .ant-menu-submenu-title .ant-menu-submenu-arrow::after,
.darkTheme .ant-menu-submenu-vertical-right > .ant-menu-submenu-title .ant-menu-submenu-arrow::after,
.darkTheme .ant-menu-submenu-inline > .ant-menu-submenu-title .ant-menu-submenu-arrow::after {
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0.65)), to(rgba(255, 255, 255, 0.65)));
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.65), rgba(255, 255, 255, 0.65));
}
.darkTheme .RA-navigator.RA-navigator-darkTheme {
  box-shadow: 0px 0px 28px #000000;
}
.darkTheme .RA-menu.RA-menu-darkTheme {
  background-color: var(--component-background);
}
